<template>
  <div id="center">
    <h1></h1>
    <h2 v-if="loading">...</h2>
    <h2 v-if="showSuccess">{{email}}</h2>
    <h2 v-if="showFail"></h2>
  </div>
</template>

<script>
import { checkEmailToken } from "@/axios/api";
//
export default {
  data() {
    return {
      loading: true,
      showSuccess: false,
      showFail: false,
      email: ""
    };
  },
  mounted() {},
  created() {
    console.log(this.$route.params.token);
    checkEmailToken({
      token: this.$route.params.token
    })
      .then(res => {
        this.email = res.data.email;
        this.loading = false;
        this.showSuccess = true;
        console.log(res, "");
      })
      .catch(err => {
        this.loading = false;
        this.showFail = true;
        console.log(err, "");
      });
  },
  methods: {}
};
</script>

<style lang="stylus" scoped>
#center {
  text-align: center;
  padding: 100px 0;
}
</style>


