<template>
  <div>
    <el-button @click="editPropertyInfo()"></el-button>
    <el-table :data="propertysInfo" border style="width: 100%">
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="ID">
              <span>{{ props.row._id || 'null' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.propertyName|| 'null' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.isPublish ? '' : '' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.isSelt ? '' : '' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.isDisabled ? '' : '' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ new Date(props.row.createTime).toLocaleString('chinese',{hour12:false}) || 'null' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ new Date(props.row.editTime).toLocaleString('chinese',{hour12:false}) || 'null' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.summary || 'null' }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column prop="_id" label="ID">
      </el-table-column>
      <el-table-column prop="propertyName" label="">
      </el-table-column>
      <!-- <el-table-column prop="email" label="" min-width="200">
      </el-table-column> -->
      <el-table-column fixed="right" label="" width="90px">
        <template slot-scope="scope">
          <el-button @click="getWantsInfo2(scope.row)" type="text" size="small"></el-button>
          <el-button @click="editPropertyInfo(scope.row)" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-dialog :title="propertyInfoDialogTitle" :visible.sync="propertyInfoDialogVisible" :append-to-body=true custom-class="property-info">
      <el-form :model="propertyInfoForm" status-icon label-width="80px" :rules="propertyInfoFormRules" ref="propertyInfoForm">
        <el-form-item label="" prop="propertyName">
          <el-input v-model="propertyInfoForm.propertyName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="summary">
          <el-input v-model="propertyInfoForm.summary"></el-input>
        </el-form-item>
        <el-form-item label="" prop="detail">
          <el-input type="textarea" :autosize="{ minRows: 2, maxRows: 4}" v-model="propertyInfoForm.detail"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-switch v-model="propertyInfoForm.isPublish"></el-switch>
        </el-form-item>
        <el-form-item label="">
          <el-switch v-model="propertyInfoForm.isSelt"></el-switch>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="postPropertyInfo2()"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <!--  -->
    <el-dialog :title="propertyWantsDialogTitle" :visible.sync="propertyWantsDialogVisible" :append-to-body=true custom-class="property-wants">
      <el-table :data="propertyWantsInfo" border style="width: 100%">
        <el-table-column prop="wanter._id" label="ID">
        </el-table-column>
        <el-table-column prop="wanter.username" label="">
        </el-table-column>
        <el-table-column prop="wanter.email" label="">
        </el-table-column>
        <el-table-column prop="wanter.telephone" label="">
        </el-table-column>
        <el-table-column prop="wanter.qqNumber" label="QQ">
        </el-table-column>
        <el-table-column prop="wanter.wechat" label="">
        </el-table-column>
        <el-table-column prop="message" label="">
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <div class="cell">{{new Date(scope.row.createTime).toLocaleString('chinese',{hour12:false})}}</div>
          </template>
        </el-table-column>
      </el-table>
    </el-dialog>
  </div>
</template>


<script>
import {
  getPropertysInfo,
  getPropertyInfo,
  postPropertyInfo,
  getWantsInfo
} from "@/axios/api";
//
export default {
  data() {
    return {
      propertyWantsInfo: [],
      propertyWantsDialogVisible: false,
      propertyWantsDialogTitle: "",
      propertyWantsInfo: [],
      propertysInfo: [],
      propertyInfoForm: {},
      propertyInfoDialogVisible: false,
      propertyInfoDialogTitle: "",
      propertyInfoFormRules: {
        propertyName: [
          { required: true, message: "", trigger: "blur" }
        ],
        summary: [{ required: true, message: "", trigger: "blur" }],
        detail: [{ required: true, message: "", trigger: "blur" }]
      }
    };
  },
  mounted() {},
  created() {
    this.getPropertysInfo2();
  },
  methods: {
    getPropertysInfo2() {
      getPropertysInfo()
        .then(res => {
          console.log(res, "");
          this.propertysInfo = res.data.propertys;
        })
        .catch(err => {
          console.log(err, "");
        });
    },
    editPropertyInfo(rowInfo) {
      if (rowInfo) {
        getPropertyInfo({
          _id: rowInfo._id
        })
          .then(res => {
            console.log(res, "");
            this.propertyInfoForm = res.data.property;
            this.propertyInfoDialogTitle =
              "ID" + res.data.property._id + "";
            this.propertyInfoDialogVisible = true;
          })
          .catch(err => {
            console.log(err, "");
          });
      } else {
        this.propertyInfoForm = {
          isPublish: true,
          isSelt: false
        };
        this.propertyInfoDialogTitle = "";
        this.propertyInfoDialogVisible = true;
      }
    },
    postPropertyInfo2() {
      this.$refs.propertyInfoForm.validate(valid => {
        if (valid) {
          postPropertyInfo(this.propertyInfoForm)
            .then(res => {
              console.log(res, "");
              this.propertyInfoDialogVisible = false;
              this.propertyInfoForm = {};
              this.getPropertysInfo2();
            })
            .catch(err => {
              console.log(err, "");
            });
        } else {
          this.$message({
            message: "",
            type: "error"
          });
        }
      });
    },
    getWantsInfo2(rowInfo) {
      if (rowInfo) {
        getWantsInfo({
          _id: rowInfo._id
        })
          .then(res => {
            console.log(res, "");
            if (res.data.wants) {
              this.propertyWantsInfo = res.data.wants;
              this.propertyWantsDialogTitle =
                "ID" + rowInfo._id + "";
              this.propertyWantsDialogVisible = true;
            }
          })
          .catch(err => {
            console.log(err, "");
          });
      }
    }
  }
};
</script>

<style lang="stylus" scoped>
.demo-table-expand {
  font-size: 0;
}

.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  min-width: 300px;
}
</style>

<style lang="stylus">
.property-wants, .property-info {
  width: 900px;
}

@media (max-width: 900px) {
  .property-wants, .property-info {
    width: 100%;
  }
}
</style>


