<template>
  <div>
    <el-table :data="allWantsInfo" border style="width: 100%">
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="ID">
              <span>{{ props.row.property._id || 'null' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.property.propertyName || 'null' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.property.isDisabled ? '' : '' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.property.isPublish ? '' : '' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.property.isSelt ? '' : '' }}</span>
            </el-form-item>
            <el-form-item label="ID">
              <span>{{ props.row.keeper._id || 'null' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.keeper.username || 'null' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.keeper.email || 'null' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.keeper.isDisabled ? '' : '' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.keeper.telephone || 'null' }}</span>
            </el-form-item>
            <el-form-item label="QQ">
              <span>{{ props.row.keeper.qqNumber || 'null' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.keeper.wechat || 'null' }}</span>
            </el-form-item>
            <el-form-item label="ID">
              <span>{{ props.row.wanter._id || 'null' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.wanter.username || 'null' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.wanter.email || 'null' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.wanter.isDisabled ? '' : '' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.wanter.telephone || 'null' }}</span>
            </el-form-item>
            <el-form-item label="QQ">
              <span>{{ props.row.wanter.qqNumber || 'null' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.wanter.wechat || 'null' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.message || 'null' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ new Date(props.row.createTime).toLocaleString('chinese',{hour12:false}) || 'null' }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column prop="property._id" label="">
      </el-table-column>
      <el-table-column prop="keeper.username" label="">
      </el-table-column>
      <el-table-column prop="wanter.username" label="">
      </el-table-column>
      <el-table-column prop="message" label="">
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <div class="cell">{{new Date(scope.row.createTime).toLocaleString('chinese',{hour12:false})}}</div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { getAllWantsInfo } from "@/axios/api";
//
export default {
  data() {
    return {
      allWantsInfo: []
    };
  },
  mounted() {},
  created() {
    this.getAllWantsInfo2();
  },
  methods: {
    getAllWantsInfo2() {
      getAllWantsInfo()
        .then(res => {
          console.log(res, "");
          this.allWantsInfo = res.data.wants;
        })
        .catch(err => {
          console.log(err, "");
        });
    }
  }
};
</script>

<style lang="stylus" scoped>
.demo-table-expand {
  font-size: 0;
}

.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  min-width: 300px;
}
</style>


