<template>
  <div class="user-status">
    <!-- <p>{{loginDialog}}</p> -->
    <el-button size="medium" icon="el-icon-yuhui44-regist" @click="registerDialogVisible=true" v-if="showLoginButton"></el-button>
    <el-button size="medium" icon="el-icon-yuhui44-login" @click="loginDialogVisible=true" v-if="showLoginButton"></el-button>
    <el-dropdown v-if="showLogoutButton">
      <el-button size="medium" icon="el-icon-yuhui44-user">{{username}}
        <i class="el-icon-arrow-down el-icon--right"></i>
      </el-button>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item @click.native="goToUserCenter()">
          
          <!-- <router-link to="/user"></router-link> -->
        </el-dropdown-item>
        <el-dropdown-item @click.native="logoutSubmit()"></el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
    <!--  -->
    <el-dialog title="" :visible.sync="registerDialogVisible" width="300px" :append-to-body=true>
      <el-form :model="registerForm" status-icon :rules="registerFormRules" ref="registerForm">
        <!--  -->
        <el-form-item prop="username">
          <el-input v-model="registerForm.username" placeholder=""></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="email">
          <el-input v-model="registerForm.email" placeholder=""></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="password">
          <el-input v-model="registerForm.password" type="password" placeholder=""></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="checkPassword">
          <el-input v-model="registerForm.checkPassword" type="password" placeholder=""></el-input>
        </el-form-item>
        <el-form-item class="floatRight">
          <el-button type="primary" @click="registerSubmit()" style="width: 260px;"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="loginDialogVisible" width="300px" :append-to-body=true>
      <el-form :model="loginForm" status-icon :rules="loginFormRules" ref="loginForm">
        <!--  -->
        <el-form-item prop="account">
          <el-input v-model="loginForm.account" placeholder=""></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="password">
          <el-input v-model="loginForm.password" type="password" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button size="mini" @click="loginToRegister()" style="float: right;"></el-button>
          <el-button size="mini" @click="showResetPass()" style="float: right;"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="loginSubmit()" style="width: 260px;"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="resetPassDialogVisible" width="300px" :append-to-body=true>
      <el-form :model="resetPassForm" status-icon :rules="resetPassFormRules" ref="resetPassForm" @submit.native.prevent>
        <!--  -->
        <el-form-item prop="account">
          <el-input v-model="resetPassForm.account" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="resetPassSubmit()" style="width: 260px;"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import {
  userRegister,
  userLogin,
  userLogout,
  userStatus,
  forgetPass,
  resendEmail
} from "@/axios/api";
export default {
  //
  created() {
    this.$root.eventHub.$on('showLogin',(target) => {
      this.loginDialogVisible = true;
    });
  },
  data() {
    //
    var validataUsername = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (!/^(?!_)(?!.*?_$)[a-zA-Z0-9_\u4e00-\u9fa5]+$/.test(value)) {
        callback(new Error(""));
      } else if (/^[0-9]+$/.test(value)) {
        callback(new Error(""));
      } else if (!/^.{4,20}$/.test(value)) {
        callback(new Error("4"));
      } else {
        callback();
      }
    };
    //
    var validataPass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (!/^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]+$/.test(value)) {
        callback(new Error(""));
      } else if (!/^.{8,20}$/.test(value)) {
        callback(new Error("8"));
      } else {
        this.$refs.registerForm.validateField("checkPassword");
        callback();
      }
    };
    //
    var validataPass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.registerForm.password) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };
    //
    var validataAccount = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (
        (!/^(?!_)(?!.*?_$)[a-zA-Z0-9_\u4e00-\u9fa5]{4,20}$/.test(value) ||
          /^[0-9]*$/.test(value)) &&
        !/^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$/.test(
          value
        )
      ) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    //
    var validataPass3 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (!/^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]+$/.test(value)) {
        callback(new Error(""));
      } else if (!/^.{8,20}$/.test(value)) {
        callback(new Error("8"));
      } else {
        callback();
      }
    };
    return {
      registerDialogVisible: false,
      registerForm: {
        username: "",
        email: "",
        password: "",
        checkPassword: ""
      },
      // registerForm: {
      //   username: "yuhui44",
      //   email: "yuhui44@live.cn",
      //   password: "ma123456",
      //   checkPassword: "ma123456"
      // },
      registerFormRules: {
        username: [
          { required: true, validator: validataUsername, trigger: "blur" }
        ],
        email: [
          { required: true, message: "", trigger: "blur" },
          {
            type: "email",
            message: "",
            trigger: "blur,change"
          }
        ],
        password: [
          { required: true, validator: validataPass, trigger: "blur" }
        ],
        checkPassword: [
          { required: true, validator: validataPass2, trigger: "blur" }
        ]
      },
      loginDialogVisible: false,
      loginForm: {
        account: "",
        password: ""
      },
      // loginForm: {
      //   account: "yuhui44",
      //   password: "ma123456"
      // },
      loginFormRules: {
        account: [
          { required: true, validator: validataAccount, trigger: "blur" }
        ],
        password: [{ required: true, validator: validataPass3, triger: "blur" }]
      },
      //
      showLoginButton: false,
      //
      showLogoutButton: false,
      //
      username: "",
      resetPassDialogVisible: false,
      resetPassForm: {
        account: ""
      },
      resetPassFormRules: {
        account: [
          { required: true, validator: validataAccount, trigger: "blur" }
        ]
      }
    };
  },
  mounted() {
    this.getUserStatus();
  },
  computed: {
    ...mapGetters({
      loginDialog: "loginDialog"
    })
  },
  watch: {
    loginDialog (val, oldval) {
      this.loginDialogVisible = true;
    }
  },
  methods: {
    ...mapActions(["setStatus", "removeStatus"]),
    registerSubmit() {
      this.$refs.registerForm.validate(valid => {
        if (valid) {
          userRegister({
            username: this.registerForm.username,
            email: this.registerForm.email,
            password: this.registerForm.password
          })
            .then(res => {
              this.registerDialogVisible = false;
              console.log(res, "");
              this.getUserStatus();
              resendEmail()
                .then(res => {
                  console.log(res, "");
                })
                .catch(err => {
                  console.log(err, "");
                });
            })
            .catch(err => {
              console.log(err, "");
            });
        } else {
          this.$message({
            message: "",
            type: "error"
          });
        }
      });
    },
    loginSubmit() {
      this.$refs.loginForm.validate(valid => {
        if (valid) {
          userLogin({
            account: this.loginForm.account,
            password: this.loginForm.password
          })
            .then(res => {
              this.loginDialogVisible = false;
              console.log(res, "");
              this.getUserStatus();
            })
            .catch(err => {
              console.log(err, "");
            });
        } else {
          this.$message({
            message: "",
            type: "error"
          });
        }
      });
    },
    logoutSubmit() {
      userLogout()
        .then(res => {
          console.log(res, "");
          this.$router.push({ path: "/" });
          this.getUserStatus();
        })
        .catch(err => {
          console.log(err, "");
          this.$message({
            message: "",
            type: "error"
          });
        });
    },
    getUserStatus() {
      userStatus()
        .then(res => {
          console.log(res, "");
          this.username = res.data.username;
          this.showLoginButton = !res.data.isLogin;
          this.showLogoutButton = res.data.isLogin;
          this.setStatus(res.data);
        })
        .catch(err => {
          console.log(err, "");
          this.$message("");
        });
    },
    goToUserCenter() {
      this.$router.push({ path: "/user" });
      // console.log("111");
    },
    showResetPass() {
      this.loginDialogVisible = false;
      this.resetPassDialogVisible = true;
    },
    resetPassSubmit() {
      this.$refs.resetPassForm.validate(valid => {
        if (valid) {
          forgetPass(this.resetPassForm)
            .then(res => {
              this.resetPassDialogVisible = false;
              console.log(res, "");
            })
            .catch(err => {
              console.log(err, "");
            });
        } else {
          this.$message({
            message: "",
            type: "error"
          });
        }
      });
    },
  loginToRegister() {
    this.loginDialogVisible = false;
    this.registerDialogVisible = true;
  }
  }
};
</script>

<style lang="stylus" scoped>
.el-dropdown-menu {
  margin-top: 0px;
}

.user-status {
  margin-right: 5px;
}

@media (max-width: 700px) {
  .el-button {
    padding: 7px 15px;
    font-size: 12px;
    border-radius: 3px;
  }
}
</style>


