import axios from 'axios';//axios
import { Message } from "element-ui";
import store from '@/store';
import * as types from '@/store/types'

export default function fetch(options) {
  return new Promise((resolve, reject) => {
    const instance = axios.create({  //instanceaxios axios
      //
      baseURL: "/api",
      timeout: 5 * 1000, // 5
      // responseType: "json",
      // withCredentials: true,
      headers: {
        'Content-Type': 'application/json;charset=UTF-8',
        // 'token_in_header': global_.token,//token
      }
    });

    //POST()
    instance.interceptors.request.use(
      config => {
        // 
        return config;
      },
      error => {
        console.log(error);
        Message({
          //  ,toast
          showClose: true,
          message: "error.data.error.message",
          type: 'error'
        });
        return Promise.reject(error);
      }
    );

    //()
    instance.interceptors.response.use(
      res => {
        //
        if (res.data) {
          //success with message
          if (res.data.code === 2) {
            Message({
              message: res.data.msg,
              type: 'success'
            });
            //success with html message
          } else if (res.data.code == 3) {
            Message({
              message: res.data.msg,
              type: 'success',
              dangerouslyUseHTMLString: true
            })
            //warning with message
          } else if (res.data.code == 4) {
            Message({
              message: res.data.msg,
              type: 'warning'
            });
            //warning with html message
          } else if (res.data.code === 5) {
            Message({
              message: error.response.data.msg,
              type: 'warning',
              dangerouslyUseHTMLString: true
            });
          }
        }
        return res;
      },
      error => {
        if (error.response.data) {
          //error with message
          if (error.response.data.code === 6) {
            Message({
              message: error.response.data.msg,
              type: 'error'
            });
            //error with html message
          } else if (error.response.data.code === 7) {
            Message({
              message: error.response.data.msg,
              type: 'error',
              dangerouslyUseHTMLString: true
            });
          } else if (error.response.data.code === 9) {
            Message({
              message: error.response.data.msg,
              type: 'error'
            });
            // this.$root.eventHub.$emit('showLogin');
            // router.replace({
            //   path: 'login',
            //   query: {redirect: router.currentRoute.fullPath}
            // });
            store.commit(types.DIALOG);
          }
        }
        return Promise.reject(error);
      }
    );

    instance(options)
      .then(response => { //then 
        resolve(response);//
      })
      .catch(error => {
        console.log('' + error);
        reject(error);
      });
  });
}


