<template>
  <div>
    <el-table :data="usersInfo" border style="width: 100%">
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="ID">
              <span>{{ props.row._id || 'null' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.username || 'null' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.email || 'null' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.telephone || 'null' }}</span>
            </el-form-item>
            <el-form-item label="QQ">
              <span>{{ props.row.qqNumber || 'null' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.wechat || 'null' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ new Date(props.row.createTime).toLocaleString('chinese',{hour12:false}) || 'null' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.message ? '' : '' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.emailConfirmation ? '' : '' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.isAdmin ? '' : '' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.isDisabled ? '' : '' }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column prop="_id" label="ID" min-width="210">
      </el-table-column>
      <el-table-column prop="username" label="" min-width="120">
      </el-table-column>
      <el-table-column prop="email" label="" min-width="200">
      </el-table-column>
      <el-table-column fixed="right" label="" width="50">
        <template slot-scope="scope">
          <el-button @click="editUserInfo(scope.row)" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-dialog title="" :visible.sync="userInfoDialogVisible" width="340px" :append-to-body=true>
      <el-form :model="userInfoForm" status-icon label-width="80px" :rules="userInfoFormRules" ref="userInfoForm">
        <el-form-item label="ID">
          <span class="el-checkbox__label">{{userInfoForm._id}}</span>
        </el-form-item>
        <el-form-item label="" prop="username">
          <el-input v-model="userInfoForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="userInfoForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="telephone">
          <el-input v-model="userInfoForm.telephone"></el-input>
        </el-form-item>
        <el-form-item label="QQ" prop="qqNumber">
          <el-input v-model="userInfoForm.qqNumber"></el-input>
        </el-form-item>
        <el-form-item label="" prop="wechat">
          <el-input v-model="userInfoForm.wechat"></el-input>
        </el-form-item>
        <el-form-item label="" prop="createTime">
          <el-date-picker v-model="userInfoForm.createTime" type="datetime"></el-date-picker>
        </el-form-item>
        <el-form-item label="">
          <el-switch v-model="userInfoForm.message"></el-switch>
        </el-form-item>
        <el-form-item label="">
          <el-switch v-model="userInfoForm.emailConfirmation"></el-switch>
        </el-form-item>
        <el-form-item label="">
          <el-switch v-model="userInfoForm.isAdmin"></el-switch>
        </el-form-item>
        <el-form-item label="">
          <el-switch v-model="userInfoForm.isDisabled"></el-switch>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="postUserInfo2()"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>


<script>
import { getUsersInfo, postUserInfo } from "@/axios/api";
//
export default {
  data() {
    //
    var validataUsername = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (!/^(?!_)(?!.*?_$)[a-zA-Z0-9_\u4e00-\u9fa5]+$/.test(value)) {
        callback(new Error(""));
      } else if (/^[0-9]+$/.test(value)) {
        callback(new Error(""));
      } else if (!/^.{4,20}$/.test(value)) {
        callback(new Error("4"));
      } else {
        callback();
      }
    };
    return {
      usersInfo: [],
      userInfoForm: {},
      userInfoDialogVisible: false,
      userInfoFormRules: {
        username: [
          { required: true, validator: validataUsername, trigger: "blur" }
        ],
        email: [
          { required: true, message: "", trigger: "blur" },
          {
            type: "email",
            message: "",
            trigger: "blur,change"
          }
        ]
      }
    };
  },
  mounted() {},
  created() {
    this.getUsersInfo2();
  },
  methods: {
    getUsersInfo2() {
      getUsersInfo()
        .then(res => {
          console.log(res, "");
          this.usersInfo = res.data.users;
        })
        .catch(err => {
          console.log(err, "");
        });
    },
    editUserInfo(rowInfo) {
      console.log(rowInfo);
      this.userInfoForm = JSON.parse(JSON.stringify(rowInfo));
      this.userInfoDialogVisible = true;
    },
    postUserInfo2() {
      postUserInfo(this.userInfoForm)
        .then(res => {
          console.log(res, "");
          this.userInfoDialogVisible = false;
          this.userInfoForm = {};
          this.getUsersInfo2();
        })
        .catch(err => {
          console.log(err, "");
        });
    },
  }
};
</script>

<style lang="stylus" scoped>
.demo-table-expand {
  font-size: 0;
}

.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  min-width: 300px;
}
</style>


